<!-- 线下传单拉新推广 -->
<template>
    <div class='offline'>
        <img v-lazy="banner" alt="" class="offline-banner" v-if="banner">
        <div class="offline-content">
            <!-- 不在进行中 -->
            <div class="offline-msg" v-if="isLogin && status===2 && err_msg">{{err_msg}}</div>
            <!-- 验证异常 -->
            <div class="offline-msg offline-error" v-if="isLogin && status===1 && err_msg">{{err_msg}}</div>
            <div :class="isClickCoupons || status===1?'offline-down':''">
                <div :class="['offline-btn',isClickCoupons || status===1?'offline-fixed':'']" v-if="isLogin && (status!==0 || isClickCoupons&&status===0)" @click="getDown">{{status===2?'下载APP领38元新人红包':'立即下载APP使用'}}</div>
            </div>
            <!-- 邀请结果 -->
            <div v-if="isLogin && isClickCoupons">
                <div class="offline-msg offline-title" v-if="coupons.length">恭喜你，成功领取红包！</div>
                <div v-if="coupons.length">
                  <div v-for="(item,index) in coupons" :key="index +'coupons'">
                      <div class="offline-coupon">
                          <div class="offline-coupon-left">
                              <div class="offline-coupon-price">¥<span :class="item.money.length < 4 ?'coupon-price':''">{{item.money}}</span></div>
                              <div class="offline-coupon-desc">满{{item.pay_min}}元使用</div>
                          </div>
                          <div class="offline-coupon-middle"></div>
                          <div class="offline-coupon-right">
                              <div class="offline-coupon-name">{{item.name}}</div>
                              <div class="offline-coupon-desc">{{item.valid_days}}天后过期</div>
                          </div>
                      </div>
                  </div>
                </div>
                <div class="offline-msg offline-title offline-msg-top" v-if="register_coupons.length && register_title">{{register_title}}</div>
                <div v-if="register_coupons.length">
                  <div v-for="(item,index) in register_coupons" :key="index+'register'">
                      <div class="offline-coupon">
                          <div class="offline-coupon-left">
                              <div class="offline-coupon-price">¥<span :class="(item.money+'').length < 4 ?'coupon-price':''">{{item.money}}</span></div>
                              <div class="offline-coupon-desc">满{{item.pay_min}}元使用</div>
                          </div>
                          <div class="offline-coupon-middle"></div>
                          <div class="offline-coupon-right">
                              <div class="offline-coupon-name">{{item.name}}</div>
                              <div class="offline-coupon-desc">{{item.valid_days}}天后过期</div>
                          </div>
                      </div>
                  </div>
                </div>
            </div>
            <!-- 邀请码 -->
            <div v-if="isLogin && !isClickCoupons &&status===0">
                <div class="offline-input-wrap">
                    <input class="offline-input" type="number" v-model="inviteCode" placeholder="请输入邀请码领红包">
                </div>
                <div :class="['offline-btn',inviteCode.length>0?'':'offline-btn-disable']" @click="getCoupons">领取红包</div>
            </div>
            <!-- 登录 -->
            <div v-if="!isLogin">
                <LoginComp @onLogin="handleLogin"></LoginComp>
            </div>
        </div>
        <div class="offline-div"></div>
        <div class="offline-notice">
            <div class="offline-notice-line"></div>
            <div class="offline-notice-item" v-html="description"></div>
        </div>
    </div>
</template>

<script>
import LoginComp from "@/components/common/LoginComp";
import API from "@/server/api";
export default {
    components: {
        LoginComp
    },
    data() {
        return {
            isLogin: false,
            inviteCode: "",
            isClickCoupons: false,
            err_msg: "",
            description: "",
            banner: "",
            status: "",
            app_url: "",
            coupons: [],
            register_coupons:[],
            register_title:''
        };
    },
    methods: {
        handleLogin() {
            //注意此处页面不刷新
            this.$store.dispatch("login/setLoginCheck").then(() => {
                this.getDetail();
            });
        },
        async getCoupons() {
            let { inviteCode } = this;
            let { mobile } = this.$store.state.login.user_info;
            this.log({
                cid: "invite_code",
                aid: "invite_code_click",
                value: mobile,
                name: inviteCode
            });
            if (!inviteCode) return;
            let { data } = await this.$http.get(
                API.eventApi("newInvite"),
                { inviteCode, type: "invite_code" },
                false
            );
            if (!data) return;
            if (data) this.isClickCoupons = true;
            this.coupons = data.coupons;
            this.register_coupons = data.register_coupons;
            this.register_title = data.register_title;
            let coupon_id = this.coupons.length === 1 ? this.coupons[0].id : "";
            this.log({
                cid: "invite_code",
                aid: "get_coupon_success",
                name: coupon_id
            });
        },
        async getDetail(value) {
            let { channel } = this.$route.query;
            let openid = value || this.$store.state.login.openid;
            let { data } = await this.$http.get(
                API.eventApi("inviteH5"),
                { openid, s: channel },
                true,
                false,
                true
            );
            if (!data) return;
            this.isLogin = this.$store.state.login.user_login;
            let { banner, description, err_msg, status, app_url } = data;
            this.banner = banner;
            this.description = description.replace(/\n/gm, "<br/>");
            this.err_msg = err_msg;
            this.status = status;
            this.app_url = app_url;
        },
        getDown() {
            this.openUrl({ path: this.app_url });
        },
        watchOpenId() {
            this.log({
                cid: "invite_code",
                aid: "index"
            });
            this.getDetail();
        }
    },
    created() {
        let openid = this.$store.state.login.openid;
        openid &&
            this.log({
                cid: "invite_code",
                aid: "index"
            });
        this.getDetail();
    }
};
</script>
