<!-- 通用登录组件(可接入弹框)  yankui.cao-->
<template>
    <div>
        <div class="login-item login-mobile">
            <input class="login-input" type="tel" v-model="mobile" placeholder="输入手机号码" maxlength="11">
            <i class="iconfont icondelete" @click="clearInput('mobile')" v-show="mobile.length"></i>
        </div>
        <div class="login-item" v-if="need_verify_code">
            <input class="login-input" type="text" v-model="verify_code" placeholder="输入图形验证码">
            <div class="login-item-right">
                <i class="iconfont icondelete" @click="clearInput('verify_code')" v-show="verify_code.length"></i>
                <div class="login-input-btn" @click="changeImg">
                    <img :src="verifyImg" class="login-img">
                </div>
            </div>

        </div>
        <div class="login-item">
            <input class="login-input" type="tel" v-model="code" placeholder="输入验证码" maxlength="4">
            <div class="login-item-right">
                <i class="iconfont icondelete" @click="clearInput('code')" v-show="code.length"></i>
                <div :class="['login-btn login-input-btn',canGetCode?'':'login-btn-disable']" @click="getSms(1)">{{time_text}}</div>
            </div>
        </div>
        <div :class="['login-item login-btn login-submit',canSubmit?'':'login-btn-disable']" @click="handleLogin">登录</div>
    </div>
</template>

<script>
import API from "@/server/api";
import { validator } from "@/utils/validator";
import { local } from "@/utils/storage";
export default {
    data() {
        return {
            mobile: "",
            verify_code: "",
            need_verify_code: 0,
            code: "",
            canGetCode: false,
            canSubmit: false,
            time_text: "获取验证码",
            timer: null,
            verifyImg: API.userApi("verifyCode?" + Math.random())
        };
    },
    props: {
        toast: Boolean,
        time: {
            type: Number,
            default: 60
        },
        source: String
    },
    watch: {
        mobile() {
            this.canGetCode = this.validateCode(true, "1234");
            this.canSubmit = this.validateCode(true);
        },
        verify_code() {
            this.canGetCode = this.validateCode(true, "1234");
            this.canSubmit = this.validateCode(true);
        },
        code() {
            this.canSubmit = this.validateCode(true);
        }
    },
    methods: {
        changeImg() {
            this.verifyImg = API.userApi("verifyCode?" + Math.random());
        },
        clearInput(value) {
            this[value] = "";
        },
        validateCode(no_toast, ignore_code) {
            const { mobile, verify_code, need_verify_code, code } = this;
            let vMobile = validator.validateMobile(mobile);
            let vCode = validator.validateSmsCode(ignore_code || code);
            let vImg = validator.validateImgCode(verify_code);
            let needVerify = need_verify_code && vImg;
            if (vMobile || needVerify || vCode) {
                this.$emit("onError", vMobile || (needVerify&&vImg) || vCode);
                return false;
            }
            return true;
        },
        async getSms(type) {
            const _that = this;
            if (!this.validateCode(this.toast, "1234") || !this.canGetCode) return;
            this.canGetCode = false;
            const { mobile, verify_code} = this;
            const { data, error } = await this.$http.get(
                API.userApi("getSms"),
                { mobile, verify_code, type},
                false,
                false
            );
            if(error){
                if(error.code === -1000){
                    const { need_verify_code } = error.data;
                    this.need_verify_code = need_verify_code;
                }
                if (error.code === 1205){
                    this.$confirm({
                        title: '建议获取语音验证码',
                        content: '收不到验证码？可尝试语音验证码，请点击确定后注意接听电话',
                        success() {
                            _that.canGetCode = true;
                            _that.getSms(3);
                            _that.log({
                                cid: 'login',
                                aid: 'get_voice_code'
                            });
                        },
                        cancel() {
                            _that.canGetCode = true;
                        }
                    });
                }else{
                    this.$toast(error.msg);
                }
            }

            data && this.countDown();
        },
        async handleLogin() {
            const { mobile, verify_code, code, source } = this;
            if (!this.validateCode(this.toast, code)) return;
            const param = {
                mobile,
                verify_code,
                code,
                source
            };
            const { data } = await this.$http.get(
                API.userApi("qlogin"),
                param,
                false
            );
            data || this.$emit("onError");
            if (data) {
                this.$emit("onLogin", data);
                local.save("USER_INFO", data);
                this.$store.state.login.user_info = data;
            }
        },
        countDown() {
            let time = this.time;
            this.time_text = `获取(${time})`;
            clearInterval(this.timer);
            this.timer = setInterval(() => {
                time--;
                this.time_text = `获取(${time})`;
                if (time < 0) {
                    clearInterval(this.timer);
                    this.time_text = "重新获取";
                    time = 60;
                    this.canGetCode = true;
                }
            }, 1000);
        }
    },
    destroyed() {
        clearInterval(this.timer);
    }
};
</script>
